@import url('https://fonts.googleapis.com/css2?family=Bungee&family=Bungee+Shade&family=Major+Mono+Display&family=Roboto:wght@100;300;400;500;700;900&display=swap');

*{
 
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

text {
  font-family: 'Bungee Shade', cursive;
}

.cardText{
  font-family: 'Major Mono Display', monospace;
  font-weight: 400;
}

.clockText{
  font-family: 'Bungee', cursive;
  font-weight: 400;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
